<template>
  <div class="musiclist">
      <!-- <h2>1</h2> -->
    <p class="nav">
      <img @click="back" src="../assets/img/back2.png" /><span> 分类歌单</span>
    </p>
    <ul class="big">
      <div class="mid">
      <li @click="cato=cat" v-for="(cat,i) in cat_list" :key="i" :class="{active:cato==cat}">{{ cat }}</li>
      </div>
    </ul>
    <div class="song">
 <pic-list v-for="item in song_list" :key="item.id" :item="item"></pic-list>
  </div>
  </div>
</template>

<script>
import PicList from "@/components/PicList";
export default {
    components:{
        PicList

    },
  data() {
    return {
      music_list: [],
      cat_list: [
        "华语",
        "流行",
        "摇滚",
        "民谣",
        "电子",
        "轻音乐",
        "综艺",
        "影视原声",
        "ACG",
      ],
      cato:"华语",
      song_list:[]
    };
  },

  created() {

    this.$axios.get("/top/playlist?cat="+this.cato).then((a) => {
            this.song_list = a.data.playlists.slice(0,48).map(s=>{
          s.picUrl = s.coverImgUrl;
          return s
      });

    });
  },
  watch:{
    cato(){
         this.$axios.get("/top/playlist?cat="+this.cato).then((aa) => {
      this.song_list = aa.data.playlists.slice(0,48).map(ss=>{
          ss.picUrl = ss.coverImgUrl;
          return ss
      });
    });
    }
  },
  methods: {
    back() {
      this.$router.back();
    },
  },
};
</script>

<style lang="less" scoped>
.musiclist {
  // width: 100vw;
  padding: 0 10px;
  .nav {
    margin: 0px auto;
    background-color: #fff;
    border-bottom: 1px solid #AECE89;
    display: flex;
    height: 40px;
    width: 100vw;
    z-index: 33;
    font-size: 20px;
    align-items: center;
    position: fixed;
    top: 0;
    left: 0;
    img {
      width: 25px;
      height: 20px;
      margin-left: 10px;
    }
    span {
      margin: 0 auto;
    }
  }

  .big {
    margin-top: 50px;
    overflow-x: auto;
    div.mid{
     width: 150%;
     display: flex;
     height: 40px;
    li {
      width: 18%;
      margin-bottom: 15px;
      text-align: center;
    }
     li.active {
        position: relative;
        color: #333;
        font-size: 15px;
        &::after {
          content: "";
          display: inline-block;
          position: absolute;
          bottom: -6px;
          left: 18px;
          width: 30px;
          height: 6px;
          border-radius: 3px;
          background-color: rgb(174, 206, 137);
        }
      }
  }
  }
  .song{
      display: flex;
      width: 100%;
      justify-content: space-around;
      flex-wrap: wrap;
      div{
          width: 31%;
      }
  }
}
</style>